/* Fonts */

/* roboto-condensed-regular - latin_cyrillic */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src:
    url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff") format("woff"); /* Modern Browsers */
  font-display: swap;
}
/* roboto-condensed-700 - latin_cyrillic */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src:
    url("../fonts/roboto-condensed-v19-latin_cyrillic-700.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/roboto-condensed-v19-latin_cyrillic-700.woff") format("woff"); /* Modern Browsers */
  font-display: swap;
}

/* pt-mono-regular - latin_cyrillic */
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src:
    url("../fonts/pt-mono-v8-latin_cyrillic-regular.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/pt-mono-v8-latin_cyrillic-regular.woff") format("woff"); /* Modern Browsers */
  font-display: swap;
}
